	module.exports = {"data":{"post":{"html":"<p>A few weeks ago it was <a href=\"http://www.instapundit.com/archives/003434.php\">reported</a> that George W. Bush \"doesn't give a shit what Europeans think\". From American reaction to the German election campaign and the remarks of Justice Minister Däubler-Gmelin comparing Bush and Hitler, that report was obviously false. The United States does care what the world thinks about it, and even a superpower has feelings that can be hurt.</p>\n<p>Däubler-Gmelin has demonstrated at least 3 degrees of stupidity during this episode (first making the remarks in the first place, in a discussion with labor representitives where she forgot the press was present, second approving the quotes for publication in person at the offices of the  \"Schwäbische Tagblatt\", and third trying to deny at a press conference yesterday that she had meant what she said). However, it was her boss, Gerhard Schröder, who created the atmosphere that made such unthinkable remarks possible. He made Iraq a campaign issue, he accused the US of pushing for a military adventure, and made political attacks on the US fashionable this election season. The minister was wrong, dead wrong, and deserves the political banishment that awaits her, but the chancellor shares much of the blame.</p>","fields":{"title":"Superpowers Have Feelings Too","date":"21 Sep 2002","url":"https://papascott.de/2002-09-21-superpowers-have-feelings-too/"}}},"pathContext":{"slug":"/2002-09-21-superpowers-have-feelings-too/","prev":{"fields":{"title":"First Movie","date":"21 Sep 2002","slug":"/2002-09-21-first-movie/"}},"next":{"fields":{"title":"Bus Driver","date":"22 Sep 2002","slug":"/2002-09-22-bus-driver/"}}}}

